.edit_data{
    width: 100%;
    border: 1px solid var(--border-color);
    background-color: var(--secondary-color);
    padding: 1em;
    border-radius: 0.6em;
    text-align: center;

    animation-name: show_message;
    animation-duration: 1s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in-out;

    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 0.5em;
}

.edit_data input{
    font-size: 2em;
    line-height: 1.4em;
    text-align: center;
    padding: 0;
    color: var(--accent-color);
    font-weight: 500;
}
.title{
    font-size: 1.6em;
    padding-bottom: 0.5em;
}

.btn{
    font-size: 2em;
}


.name{
    font-size: 1.4em;
    color:var(--accent-color);
}
.dop_info{
    font-size: 1.4em;
    color:var(--primary-color);
}
