.edit_data{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.2em;
    margin: 0.5em;
}
.title{
    padding-bottom: 1em;
    color:var(--accent-color);
    font-size: 1.8em;
}
.row{
    width: 100%;
    display: flex;
    justify-content: space-between;
    gap:0.2em;
    padding: 0.2em 0;

    border:1px solid var(--accent-color);
    border-radius: 0.2em;
}
.name{
    font-size: 1.2em;
    color:var(--accent-color);
}
.dop_info{
    font-size: 1em;
    color:var(--primary-color);
}
.item{
    width: 100%;
    text-align: center;

}
.active{
    background-color: #dda94b;
}